@use "sass:map";

.result {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding: 15rem 5.7rem 6rem;
  width: 100%;

  @media (min-width: $stop-scaling) {
    padding: 150px 57px 60px;
  }

  @media (orientation: portrait) {
    padding: 8rem 0 4rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    padding: 8rem 0 3rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    padding: 3rem 0;
  }
}

.result__image {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: -1;

  @media (orientation: portrait) {
    left: -1rem;
    right: -1rem;
  }

  picture {
    display: block;
    width: 100%;
    height: 100%;
  }

  img {
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}

.result__title {
  margin: 0 auto 2rem;
  color: $c-light;

  @media (min-width: $stop-scaling) {
    margin: 0 auto 20px;
  }

  @media (orientation: portrait) {
    margin-bottom: 1.5rem;
  }

  svg {
    display: block;
    width: 100%;
    height: 100%;
    stroke-width: 0.2rem;
    stroke-linejoin: round;
  }
}

.result__text {
  margin-bottom: 2rem;
  text-align: center;
  font-family: $font-alt;
  font-weight: 400;
  font-size: 4.8rem;
  line-height: 0.906;
  letter-spacing: -0.01em;

  @media (min-width: $stop-scaling) {
    margin-bottom: 20px;
    font-size: 48px;
  }

  @media (orientation: portrait) {
    font-size: 2.8rem;
    line-height: 1.1;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    font-size: 2.8rem;
  }

  p {
    margin: 0;
  }
}

.result__button {
  @include btn-reset;
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: center;
  margin: auto auto 1rem;
  min-width: 34rem;
  text-transform: uppercase;
  font-size: 2.4rem;
  line-height: 1.2;
  color: $c-light;

  @media (min-width: $stop-scaling) {
    margin: auto auto 10px;
    min-width: 340px;
    font-size: 24px;
  }

  @media (orientation: portrait) {
    margin: auto 0 0;
    transform: none;
    width: 100%;
    min-width: auto;
    font-size: 1.6rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    transform: none;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    margin: auto 0 0;
    width: 100%;
    min-width: auto;
    font-size: 1.6rem;
  }

  span {
    position: relative;
    margin-left: 1.5rem;
    width: 4rem;
    height: 4rem;
    color: $c-dark;

    @media (min-width: $stop-scaling) {
      margin-left: 15px;
      width: 40px;
      height: 40px;
    }

    &::before {
      @include center;
      content: "";
      width: 100%;
      height: 100%;
      background-color: $c-light;
      border: 2px solid $c-light;
      border-radius: 50%;
      z-index: -1;
    }
  }

  svg {
    @include center;
    margin-left: 0.3rem;
    width: 2.4rem;
    height: 1.9rem;

    @media (min-width: $stop-scaling) {
      margin-left: 3px;
      width: 24px;
      height: 19px;
    }
  }

  &:hover {
    span::before {
      width: 112.5%;
      height: 112.5%;
    }
  }

  &:focus {
    outline: none;

    span::before {
      border-color: $c-purple;
    }
  }

  &:active,
  &:active:focus {
    span {
      color: $c-light;
    }

    span::before {
      width: 100%;
      height: 100%;
      background-color: $c-purple;
      border-color: $c-purple;
    }
  }
}

.result__form {
  margin-top: auto;

  .field-wrapper {
    position: relative;
    margin-right: 1.5em;
    padding: 0;

    @media (max-width: $mobile) {
      margin-right: 1rem;
    }
  }

  input.form__field {
    width: 100%;
    transform-origin: 97%;
    animation: expandWidth map.get($animation-durations, "slow");
    animation-delay: map.get($animation-durations, "delayed");
    animation-fill-mode: both;
    animation-timing-function: ease-out;

    @media (max-width: $mobile) {
      transform-origin: 92%;
    }
  }

  .field-border {
    position: absolute;
    height: 6rem;
    width: 68px;
    top: 0;
    right: 0;
    background: white;
    z-index: -1;
    font-size: 2.4em;
    border-radius: 3rem;
    animation: fadeIn;
    animation-delay: map.get($animation-durations, "delayed");
    animation-fill-mode: both;

    @media (max-width: $mobile) {
      height: 4rem;
      width: 100px;
    }

  }

  .field-cover {
    position: absolute;
    height: 6rem;
    background: white;
    width: 92%;
    left: 5%;
    top: 0;

    transform-origin: 100%;
    animation: expandWidth map.get($animation-durations, "slow") map.get($animation-durations, "delayed"), fadeOut map.get($animation-durations, "medium") map.get($animation-durations, "slowest"); 
    animation-fill-mode: both;
    animation-timing-function: ease-out;

    @media (max-width: $mobile) {
      height: 4rem;
      left: 2.5rem;
      width: 80%;
    }
  }

  button{
    animation: fadeInColor map.get($animation-durations, "slow");
    animation-delay: map.get($animation-durations, "extraSlow");
    animation-fill-mode: both;

    &::before {
      animation: fadeIn map.get($animation-durations, "delayed");
      animation-delay: map.get($animation-durations, "slow");
      animation-fill-mode: both;
    }
  }
}

.result--trip,
.result--prize {
  
  .result__image {
    img {
      width: unset;
      height: unset;
    }

    .flamingo {
      transform: translate3d(-30%, 20%, 0);
      width: 200px;
      z-index: 2;
      animation: slideUpFlamingo map.get($animation-durations, "delayed");
      animation-fill-mode: both;
      animation-timing-function: $over-expand-timing-function;
    }

    .flamingo-wrapper {
      transform-origin: -1%;
      width: 200px;
      height: 200px;
      z-index: 2;
      animation: flamingoSpin 1s ease 0s infinite alternate;
    }

    .suitcase {
      width: 300px;
      animation: slideUpSuitcase map.get($animation-durations, "delayed");
      animation-fill-mode: both;
      animation-timing-function: $over-expand-timing-function;
    }

    .leaf-blue-1 {
      transform: rotate(0deg) translate3d(50%, -10%, 0);
      width: 250px;
      animation: fadeInLeaf-1 map.get($animation-durations, "slowest");
      animation-fill-mode: both;
      animation-timing-function: ease;
    }

    .leaf-blue-2 {
      transform: rotate(0deg) translate3d(-70%, 30%, 0);
      width: 250px;
      animation: fadeInLeaf-2 map.get($animation-durations, "slowest");
      animation-fill-mode: both;
      animation-timing-function: ease;
    }

    .leaf-green {
      transform: translate3d(65%, 30%, 0);
      width: 250px;
      animation: fadeInLeaf-green map.get($animation-durations, "slowest");
      animation-fill-mode: both;
      animation-timing-function: ease;
      animation-delay: map.get($animation-durations, "fast");
    }
  }

  .result__title {
    width: 57.3rem;
    height: 11.3rem;

    @media (min-width: $stop-scaling) {
      width: 573px;
      height: 113px;
    }

    @media (orientation: portrait) {
      width: 28.7rem;
      height: 5.7rem;
    }

    @media (max-width: $tablet) and (orientation: landscape) {
      width: 40rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      margin-bottom: 0;
      width: 35rem;
    }
  }
}

.result--negative {
  .result__button {
    animation: fadeIn map.get($animation-durations, "slow");
    animation-delay: map.get($animation-durations, "delayed");
    animation-fill-mode: both;

    span {
      animation: resultButtonRoll map.get($animation-durations, "delayed");
      animation-delay: map.get($animation-durations, "delayed");
      animation-fill-mode: both;
    }
  }

  .result__title {
    margin-top: 9.5rem;
    width: 66.5rem;
    height: 11.3rem;

    @media (min-width: $stop-scaling) {
      margin-top: 95px;
      width: 665px;
      height: 113px;
    }

    @media (orientation: portrait) {
      margin-top: 0;
      width: 28.8rem;
      height: 4.9rem;
    }

    @media (max-width: $tablet) and (orientation: landscape) {
      margin-top: 0;
      width: 50rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      width: 45rem;
    }
  }
}
